/*basic styles*/
    *{
        box-sizing: border-box;
        transition: .5s;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'PT Sans', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
    }

    p{
        margin: 0;
        padding: 0;
    }

    .wrapper{
        position: relative;
        height: 100%;
        width: 100%;
        overflow-x: hidden;
    }

    .section{
        height: 100%;
        width: 100%;
    }

    .container{
        width: 960px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include smallmonitor{
            padding: 0 5%;
        }
    }

    .button{
        padding: 10px 15px;
        border: 1px solid transparent;
        border-radius: 30px;
        background-color: #50c3af;
        color: #fff;
        font-weight: 400;
        display: inline-block;
        outline: none;
        cursor: pointer;

        &:hover{
            background-color:#76cfd7;
        }

        &--bigger{
            padding: 20px 35px;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 15px;
        }

        &--bordernone{
            background-color: transparent;
            padding: 10px;
            color: #50c3af;

            &:hover{
                color:#fff;
            }
        }

    }

/*base end*/

/************************************/

/*header*/
  
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom:75px;
    padding-top: 60px;

    &__logo{
        margin-bottom: 40px;
    }
}  

/*header end*/


/*intro section*/

.intro{

    background: url(../img/intro-bg.png) center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    min-height: 600px;

    &__wrapp{
        height: 100%;
        width: 100%;
    }
}
/*************************************/

/*menu block*/

.menu{

    &__list{
        display: flex;
    }

    &__item{
        margin-right: 40px;

        &:last-child{
            margin-right: 0;
        }
    }

    &__link{
        color: #fff;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600;
        font-family: 'Open Sans', sans-serif;

        &:hover{
            color:grey;
        }
    }

    @include tablets{
        display: none;
    }
}
/*************************************/

/*logo block*/
.logo{

    &__link{
        display: block;
    }

    &__img{
        max-height: 100%;
        max-width: 100%;
    }
}

/*************************************/
/*main block*/

.main{
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    font-weight: 700;

    &__title{
        font-size: 53px;
        margin-bottom: 40px;
        font-family: 'Ubuntu';
    }

    &__subtitle{
        display: inline-block;
        font-size: 15px;
        margin-bottom: 70px;

        &:after, &:before{
            content:'';
            display: inline-block;
            vertical-align: middle;
            width: 100%;
            height: 3px;
            background-color: #476269;
        }

        &:before{
            margin-left: -100%;
        }

        &:after{
            margin-right: -100%;
        }
    }
}
/*************************************/


/*features section*/

.features{
    padding: 50px 0 20px 0;
    background-color: #eeeeef;
    
    &__list{
        display: flex;

        @include phones{
            flex-wrap: wrap;
        }
    }

    &__item{
        margin-right: 20px;

        &:last-child{
            margin-right: 0;
        }

        @include phones{
            width: 100%;
            margin-bottom: 25px;
            margin-right: 0;

            &:last-child{
                margin-bottom:0;
            }
        }
    }
}

/*************************************/

/*content block*/

.content{

    &__icon{
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        margin-bottom: 25px;

        &-img{
            max-width: 100%;
            max-height: 100%;
        }
    }

    &__image{
        margin-bottom: 40px;

        &-picture{
            max-width: 100%;
            max-height: 100%;
        }
    }

    &__title{
        display: inline-block;
        font-size: 18px;
        font-weight: 700;
        text-transform: uppercase;
        color: #40474e;
        padding-bottom: 20px;
        border-bottom: 1px solid #ebebeb;
        margin-bottom: 25px;

        &--white{
            color: #fff;
        }
    }

    &__info{
        margin-bottom: 25px;
        font-weight: 400;
        color: #787b80;
        line-height: 24px;
    }
}

/************************************/

/*services section*/

.services{
    padding: 65px 0;
    background-color: #f5f5f5;

    &__list{
        display: flex;

        @include phones{
            flex-wrap: wrap;
        }
    }

    &__item{
        margin-right: 50px;

        &:last-child{
            margin-right:0;
        }

        @include phones{
            width: 100%;
            margin-right: 0;
            margin-bottom: 25px;

            &:last-child{
                margin-bottom:0;
            }
        }
    }
}
/************************************/

/*support section*/

.support{
    padding: 50px 0;
    background-color: #eeeeef;

    &__content{
        .content{

            &__title{
                padding: 0;
                border-bottom: none;
            }
        }

        @include phones{
            text-align: center;
        }
    }
}

/************************************/

/*about section*/

.about{
    padding: 50px 0;
    background-color: #f5f5f5;

    &__list{
        display: flex;

        @include tablets{
            flex-wrap: wrap;
        }
    }

    &__item{
        margin-right: 30px;

        @include tablets{
            width: 100%;
            margin-bottom: 35px;
            margin-right: 0;    
        }

        &:last-child{
            margin-bottom: 0; 
        }

        &--size{
            .content{
                display: flex;
                flex-direction: column;
                align-items: center;

                &__image{
                    width: 240px;
                    height: 240px;

                    &-picture{
                        max-width: 100%;
                        max-height: 100%;
                    }
                }
            }
        }
    }
}
/************************************/

/*contacts section*/

.contacts{
    padding: 50px 0;
    background-color: #50c3af;

    &__content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__company-list{
        display: flex;

        @include tablets{
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    &__company-item{
        margin-right: 55px;

        &:last-child{
            margin-right: 0;   
        }

        &-icon{
            max-height: 100%;
            max-width: 100%;
        }

        @include tablets{
            margin-right: 20px;
        }

        @include phones{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 49%;
            margin-right: 0;
            margin-bottom: 15px;

            &:nth-child(3){
                margin-bottom:0;
            }

            &:last-child{
                margin-bottom:0;
            }
        }
    }
}

/***********************************/

/*fullscreen section*/

.fullscreen{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    overflow: hidden;
    opacity: 1;
    background-color: #132f3f;

    opacity: 0;
    visibility: hidden;

    transition-delay: .5s;

    .logo{
        position: absolute;
        top: 20px;
        left: 50%;
        transform: translateX(-50%);
    }

    &__menu{
        width: 100%;
        height: 100%;

        .menu{
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            &__list{
                flex-direction: column;
                align-items: center;
            }

            &__item{
                margin-right: 0;
                margin-bottom: 20px;

                &:last-child{
                    margin-bottom:0;
                }
            }

            &__link{
                font-size: 30px;
            }
        }
    }
}

.is-active{
    opacity: 1;
    visibility: visible;
    transition-delay: .2s;
}

/***********************************/

/*burger icon*/
.burger{
    display: none;
    height: 25px;
    width: 30px;

    position: fixed;
    top: 30px;
    right: 20px;

    z-index: 15;

    cursor: pointer;
    

    .line{
        background-color: #fff;
        display: inline-block;
        width: 30px;
        height: 4px;
        border: 1px solid black;
        border-radius: 2px;

        position: absolute;
        top: 0;
        left: 0;

        &:nth-child(1){

        }

        &:nth-child(2){
            top: 10px;
        }

        &:nth-child(3){
            top: 20px;
        }
    }

    @include tablets{
        display: block;
    }
}

// анимация в крестик
.getCross{

    .line{
        display: inline-block;

        background-color: #fff;
        &:nth-child(1){
            display: none;
        }

        &:nth-child(2){
            transform: rotate(45deg) translateY(-50%);
            top: 50%;
        }

        &:nth-child(3){
            transform: rotate(-45deg) translateY(-50%);
            top: 50%;
        }
    }
}
/******************************/
