/*basic styles*/

* {
  box-sizing: border-box;
  transition: .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'PT Sans', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.section {
  height: 100%;
  width: 100%;
}

.container {
  width: 960px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.button {
  padding: 10px 15px;
  border: 1px solid transparent;
  border-radius: 30px;
  background-color: #50c3af;
  color: #fff;
  font-weight: 400;
  display: inline-block;
  outline: none;
  cursor: pointer;
}

.button:hover {
  background-color: #76cfd7;
}

.button--bigger {
  padding: 20px 35px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
}

.button--bordernone {
  background-color: transparent;
  padding: 10px;
  color: #50c3af;
}

.button--bordernone:hover {
  color: #fff;
}

/*base end*/

/************************************/

/*header*/

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 75px;
  padding-top: 60px;
}

.header__logo {
  margin-bottom: 40px;
}

/*header end*/

/*intro section*/

.intro {
  background: url(../img/intro-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  min-height: 600px;
}

.intro__wrapp {
  height: 100%;
  width: 100%;
}

/*************************************/

/*menu block*/

.menu__list {
  display: flex;
}

.menu__item {
  margin-right: 40px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  color: #fff;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
}

.menu__link:hover {
  color: grey;
}

/*************************************/

/*logo block*/

.logo__link {
  display: block;
}

.logo__img {
  max-height: 100%;
  max-width: 100%;
}

/*************************************/

/*main block*/

.main {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
}

.main__title {
  font-size: 53px;
  margin-bottom: 40px;
  font-family: 'Ubuntu';
}

.main__subtitle {
  display: inline-block;
  font-size: 15px;
  margin-bottom: 70px;
}

.main__subtitle:after,
.main__subtitle:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 3px;
  background-color: #476269;
}

.main__subtitle:before {
  margin-left: -100%;
}

.main__subtitle:after {
  margin-right: -100%;
}

/*************************************/

/*features section*/

.features {
  padding: 50px 0 20px 0;
  background-color: #eeeeef;
}

.features__list {
  display: flex;
}

.features__item {
  margin-right: 20px;
}

.features__item:last-child {
  margin-right: 0;
}

/*************************************/

/*content block*/

.content__icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-bottom: 25px;
}

.content__icon-img {
  max-width: 100%;
  max-height: 100%;
}

.content__image {
  margin-bottom: 40px;
}

.content__image-picture {
  max-width: 100%;
  max-height: 100%;
}

.content__title {
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: #40474e;
  padding-bottom: 20px;
  border-bottom: 1px solid #ebebeb;
  margin-bottom: 25px;
}

.content__title--white {
  color: #fff;
}

.content__info {
  margin-bottom: 25px;
  font-weight: 400;
  color: #787b80;
  line-height: 24px;
}

/************************************/

/*services section*/

.services {
  padding: 65px 0;
  background-color: #f5f5f5;
}

.services__list {
  display: flex;
}

.services__item {
  margin-right: 50px;
}

.services__item:last-child {
  margin-right: 0;
}

/************************************/

/*support section*/

.support {
  padding: 50px 0;
  background-color: #eeeeef;
}

.support__content .content__title {
  padding: 0;
  border-bottom: none;
}

/************************************/

/*about section*/

.about {
  padding: 50px 0;
  background-color: #f5f5f5;
}

.about__list {
  display: flex;
}

.about__item {
  margin-right: 30px;
}

.about__item:last-child {
  margin-bottom: 0;
}

.about__item--size .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about__item--size .content__image {
  width: 240px;
  height: 240px;
}

.about__item--size .content__image-picture {
  max-width: 100%;
  max-height: 100%;
}

/************************************/

/*contacts section*/

.contacts {
  padding: 50px 0;
  background-color: #50c3af;
}

.contacts__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contacts__company-list {
  display: flex;
}

.contacts__company-item {
  margin-right: 55px;
}

.contacts__company-item:last-child {
  margin-right: 0;
}

.contacts__company-item-icon {
  max-height: 100%;
  max-width: 100%;
}

/***********************************/

/*fullscreen section*/

.fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  overflow: hidden;
  opacity: 1;
  background-color: #132f3f;
  opacity: 0;
  visibility: hidden;
  transition-delay: .5s;
}

.fullscreen .logo {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.fullscreen__menu {
  width: 100%;
  height: 100%;
}

.fullscreen__menu .menu {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fullscreen__menu .menu__list {
  flex-direction: column;
  align-items: center;
}

.fullscreen__menu .menu__item {
  margin-right: 0;
  margin-bottom: 20px;
}

.fullscreen__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen__menu .menu__link {
  font-size: 30px;
}

.is-active {
  opacity: 1;
  visibility: visible;
  transition-delay: .2s;
}

/***********************************/

/*burger icon*/

.burger {
  display: none;
  height: 25px;
  width: 30px;
  position: fixed;
  top: 30px;
  right: 20px;
  z-index: 15;
  cursor: pointer;
}

.burger .line {
  background-color: #fff;
  display: inline-block;
  width: 30px;
  height: 4px;
  border: 1px solid black;
  border-radius: 2px;
  position: absolute;
  top: 0;
  left: 0;
}

.burger .line:nth-child(2) {
  top: 10px;
}

.burger .line:nth-child(3) {
  top: 20px;
}

.getCross .line {
  display: inline-block;
  background-color: #fff;
}

.getCross .line:nth-child(1) {
  display: none;
}

.getCross .line:nth-child(2) {
  transform: rotate(45deg) translateY(-50%);
  top: 50%;
}

.getCross .line:nth-child(3) {
  transform: rotate(-45deg) translateY(-50%);
  top: 50%;
}

/******************************/

/**********************************/

@media screen and (max-width: 992px) {
  .container {
    padding: 0 5%;
  }
}

@media screen and (max-width: 768px) {
  .menu {
    display: none;
  }

  .about__list {
    flex-wrap: wrap;
  }

  .about__item {
    width: 100%;
    margin-bottom: 35px;
    margin-right: 0;
  }

  .contacts__company-list {
    flex-wrap: wrap;
    justify-content: center;
  }

  .contacts__company-item {
    margin-right: 20px;
  }

  .burger {
    display: block;
  }
}

@media screen and (max-width: 480px) {
  .features__list {
    flex-wrap: wrap;
  }

  .features__item {
    width: 100%;
    margin-bottom: 25px;
    margin-right: 0;
  }

  .features__item:last-child {
    margin-bottom: 0;
  }

  .services__list {
    flex-wrap: wrap;
  }

  .services__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  .services__item:last-child {
    margin-bottom: 0;
  }

  .support__content {
    text-align: center;
  }

  .contacts__company-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 49%;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .contacts__company-item:nth-child(3) {
    margin-bottom: 0;
  }

  .contacts__company-item:last-child {
    margin-bottom: 0;
  }
}